import React, {useState, useEffect} from 'react';
import './style.scss';
import {basicURL, toast, parseQuery, cracyTxidCache} from "../../utils";
import {getBalance, getPrice, openUpCert} from "../../service";
import {connect, Card, Input, MaskLoading} from "../../components"
let packageJson = require('../../../package.json');

function Home({address}) {
    const invite = parseQuery.getQuery('invite') || '';
    const [balance, setBalance] = useState(0); //余额
    const [btcLight, setBtcLight] = useState(''); //合约地址
    const [invitedAddress, setInvitedAddress] = useState(invite); //邀请地址
    const [certPrice, setCertPrice] = useState(0); //证书价格
    const [btcPrice, setBrcPrice] = useState(0); //btc价格
    // const [isOpened, setIsOpened] = useState(false); //已开通
    const [isLoading, setIsLoading] = useState(false); //开通loading
    const [code, setCode] = useState(''); //邀请人
    const [openStatus, setOpenStatus] = useState(-1); //开通状态 -1:未开通 0:开通中 1:已开通

    function getIdentity () {
        getBalance(address).then(({data}) => {
            // console.log(data);
            if (data.ret) {
                let {balance, btclight, code, time, status} = data.data;
                // time = undefined
                // status = undefined

                setBalance(balance);
                setBtcLight(btclight);
                setCode(code);
                if (time) {
                    setOpenStatus(status === undefined ? 1 : status);
                }
            }
        })
    }
    function getCertPrice (addr) {
        getPrice(addr || address).then(({data}) => {
            // console.log(data);
            if (data.ret) {

                if (addr) {
                    toast('邀请地址未开通')
                } else {
                    setCertPrice(data.data.value);
                    setBrcPrice(data.data.btcusdt);
                }
            } else if(data.data === 'do not repeat opening') {
                if (addr) {
                    openUp();
                } else {
                    setOpenStatus(1)
                }

            }
        })
    }

    useEffect(() => {
        if (!address) return;
        getIdentity();
        getCertPrice();
        let txid = cracyTxidCache.get(address);
        txid && openUpFromTxid(txid);

    }, [address]);

    function submit  () {
        if(openStatus !== -1) {
            return
        }
        if (!certPrice) {
            toast('正在获取证书价格，请稍后再试。');
            return
        }
        if (!(balance >= certPrice)) {
            toast('余额不足');
            return
        }
        if (invitedAddress) {
            if(/^Sc.+/.test(invitedAddress + '')) {
                getCertPrice(invitedAddress);
            } else {
                toast('请输入正确的邀请码');
            }

        } else {
            openUp();
        }
    }

    function openUp () {
        window.demos.contract("pay", btcLight, [{type: "uint256", value: certPrice}, {type:"string", value: "[2]"}]).then(function (data){
            cracyTxidCache.set(address, data);
            openUpFromTxid(data);
        }, function (err) {
            toast(JSON.stringify(err), 3000);
        });
    }

    function openUpFromTxid(txid) {
        setIsLoading(true);
        openUpCert({
            address,
            addr: invitedAddress,
            rawtx: txid
        }).then(function ({data}) {
            setIsLoading(false);
            cracyTxidCache.remove(address);

            if(data.ret) {
                toast('开通成功');
                // setIsOpened(true);
                setOpenStatus(0);
                getIdentity();
            } else {
                toast(data.data);
            }
        }, function (err) {
            setIsLoading(false);
            toast(JSON.stringify(err), 3000);
        })
    }

    function changeAddress (e) {
        // setInvitedAddress(e.target.value.trim());
        setInvitedAddress(typeof e === 'string' ? e : e.target.value.trim());
    }

    function scan () {
        window.demos.scanQRCode().then(function (data){
            let addr = data;
            if(data.includes('invite=')) {
                addr = data.match(/invite=(\w+)/)[1]
            }
            setInvitedAddress(addr);
        });
    }

    function fundManager() {
        window.demos.openFundManager();
    }

    async function paste() {
        function focus () {
            document.getElementById('p-home-input').focus();
        }
        if(!invitedAddress) {
            try {
                const value = await window.demos.getClipboard();
                value ? setInvitedAddress(value) : focus();
            } catch (e) {
                focus();
            }
        } else {
            focus();
        }
    }

    return (
        <div className='p-home'>
            <img src={`${basicURL.imgSrc}/icon_btc.png`} alt="btc" className='coin-icon'/>
            <p className='balance'>BTC余额:<span>{balance/1e8}</span></p>
            <div className='btns'>
                <button className='c-btn' onClick={fundManager}>充值</button>
                <button className='c-btn' onClick={fundManager}>提现</button>
            </div>
            <p className='balance'>BTC价格:<span>$ {btcPrice}</span></p>
            <p className='price'>需支付:<span>{certPrice/1e8} <i>BTC</i></span></p>
            <p className='notice'>注意:BTC充值有矿工费用</p>
            {(() => {
                if(openStatus !== -1) {
                    return <Card className='invite'>推荐地址: {code || '空'}</Card>
                }

                if(invite) {
                    return <Card className='invite'>推荐地址: {invite}</Card>
                }

                return (
                    <div className='input'>
                        <div className='input-wrapper'>
                            <Input
                                type="text"
                                placeholder='推荐地址'
                                onChange={changeAddress}
                                value={invitedAddress}
                                id='p-home-input'/>
                            {/*<i onClick={paste} />*/}
                        </div>
                        <img src={`${basicURL.imgSrc}/icon_scan.png`} alt="scan" onClick={scan}/>
                    </div>
                )
            })()}
            {isLoading && <MaskLoading />}
            <button className='c-btn' onClick={submit} disabled={isLoading}>
                {/*{isOpened
                    ? '已开通'
                    : isLoading
                        ? 'loading'
                        : '开通'
                }*/}
                {openStatus === -1
                    ? isLoading
                        ? 'loading'
                        : '开通'
                    : openStatus === 1
                        ? '已开通'
                        : '处理中'
                }
            </button>
            <div className='package-json'>version: {packageJson.version}</div>
        </div>
    )
}

export default connect()(Home);
